<template>
    <div class="chart-container">
      <!-- 玫瑰图的容器 -->
      <div ref="roseChart" class="chart"></div>
    </div>
  </template>
  
  <script>
  import * as echarts from 'echarts';
  
  export default {
    name: 'RoseChart',
    mounted() {
      this.initChart();
    },
    methods: {
      initChart() {
        // 基于 DOM 初始化 ECharts 实例
        const chartDom = this.$refs.roseChart;
        const myChart = echarts.init(chartDom);
  
        // 随机生成五个数据的玫瑰图配置
        const option = {
          title: {
            left: 'center',
            textStyle: {
              fontSize: 24,
            },
          },
          tooltip: {
            trigger: 'item',
          },
          legend: {
            top: '90%',
            left: 'center',
            orient: 'horizontal', // 水平布局
            itemWidth: 14, // 标注图标的宽度
            itemHeight: 14, // 标注图标的高度
            itemGap: 20,  // 每个标注之间的间距
            width: '27%',
            formatter: function (name) {
            return name; // 保持标注文本格式
            },
          },
          series: [
            {
              name: '平均票房(每部)',
              type: 'pie',
              radius: [20, 100], // 内外半径
              center: ['50%', '50%'],
              roseType: 'radius', // 玫瑰图模式
              itemStyle: {
                borderRadius: 8, // 柱形圆角
              },
              data: [
                { value: 91.35, name: '名侦探柯南' },
                { value: 161.7, name: '新海诚三部曲系列' },
                { value: 148.22, name: '灌篮高手系列' },
                { value: 108.76, name: '海贼王' },
                { value: 74.2, name: '哆啦A梦系列' },
                { value: 150.33, name: '宫崎骏系列' },
              ],
            },
          ],
        };
  
        // 使用刚指定的配置项和数据渲染图表
        myChart.setOption(option);
  
        // 监听窗口大小变化，自动适配图表大小
        window.addEventListener('resize', () => {
          myChart.resize();
        });
      },
    },
  };
  </script>
  
  <style scoped>
  .chart-container {
    position: absolute;
    top:21.6cm;
    left: 15.4cm;
    width: 100%;
    height: 500px; /* 图表高度 */
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .chart {
    width: 80%;
    height: 100%;
  }
  </style>
  